.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-color: bisque;
}

h1 {
  font-size: 50px;
  color: rgb(55, 92, 78)
}

#balance {
  font-weight: bold;
  font-size: 30px;
}

#home {
  font-size: 20px;
}

li {
  list-style: none;
  margin-bottom: 10px;
}

#content {
  width: 50%;
  float: left;
}

#value {
  width: 50%;
  float: right;
  padding: 100px 0;
}